export default function Header(props) {
  return (
    <div id="Header">
      <div className="HeaderName">
        <h1>{props.name}</h1>
      </div>
      <div className="HeaderButtons" id="HeaderButtons">
        <button className="HeaderButton" id="WebDev">
          <strong>{props.skillOne}</strong>
        </button>
        <button className="HeaderButton" id="Blockchain">
          <strong>{props.skillTwo}</strong>
        </button>
        <button className="HeaderButton" id="GameDev">
          <strong>{props.skillThree}</strong>
        </button>
      </div>
    </div>
  );
}
